import {ajaxPost} from "./request"

//
function register(data,config){
	let url = "/register"
	return new Promise((resolve)=>{
		resolve(ajaxPost(url,data,config))
	})
}

//
function sendCode(data,config){
	let url = "/sendCode"
	return new Promise(function(resolve){
		resolve(ajaxPost(url,data,config))
	})
}

//
function login(data,config){
	let url =  "/login"
	return new Promise((resolve)=>{
		resolve(ajaxPost(url,data,config))
	})
}
//
function list(data,config){
	let url = "/system/user/list"
	return new Promise((resolve)=>{
		resolve(ajaxPost(url,data,config))
	})
}

//
function deleteUser(data,config){
	let url = "/system/user/delete"
	return new Promise((resolve)=>{
		resolve(ajaxPost(url,data,config))
	})
}

export {
	register,
	login,
	sendCode,
	list,
	deleteUser
}
